<template>
  <div class="bianji_wrap">
   <headers :title="title"></headers>
   <p class="success">完成</p>
    <div class="lanmu">
      <van-switch-cell  v-model="checked" title="特权上新" />
      <van-switch-cell  v-model="checked2" title="孕娘签单" />
      <van-switch-cell  v-model="checked3" title="共享好物" />
    </div>
  </div>
</template>
<script>
  import "lib-flexible"
  import headers from '../components/editHeader.vue'
  export default {
    data() {
      return {
        checked: true,
        checked2: true,
        checked3: true,
        title: "编辑栏目"
      };
    },
    components: {
      headers
    }
  };
</script>
<style scoped>
  .bianji_wrap {
    flex: 1;
  }

  .success {
    color: #fff;
    font-size: 36px;
    position: absolute;
    right: 40px;
    top: 34px;
  }

  .lanmu {
    width: 95%;
    float: right;
    margin-top: 90px;
  }
</style>



